import { TableCell, TableRow } from '@mui/material';
import Pagination from '../../components/Pagination/Pagination';
import React, { useEffect, useState } from 'react';
import { useTranslation } from "react-i18next";
import useRequest from '../../hooks/use-request';
import Table2 from '../../components/Table/Table2';
import Loading from '../../components/Loading/Loading';
import Notification from '../../components/Notification/Notification';

const ControlRoom = ({token}) => {
    const { t } = useTranslation();
    const [rooms, setRooms] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [count, setCount] = useState(0);
    const [allPages, setAllPages] = useState(0);

    const getRoomsRequest = useRequest({
        url: `/room?notNull=true&limit=10&page=${currentPage}`,
        method: "get",
        headers: {
          Authorization: `Bearer ${token}`,
        },
        body: {},
        onSuccess: (data) => {
          setCount(data.count);
          setAllPages(data.allPages);
          setRooms(data.data);
        },
      });

      const updateRoomDataReques = useRequest({
        url: "/room/admin",
        method: "put",
        headers: {
          Authorization: `Bearer ${token}`,
        },
        body: {},
        onSuccess: (data) => {
          getRoomsRequest.doRequest()
        },
      });  

    const updateRoomData = async (id) => {
        if(window.confirm(t("are_you_sure"))) {
            await updateRoomDataReques.doRequest(id)
        }
    }
    const changeCurrentPage = async (page) => {
        setCurrentPage(page);
      };

    useEffect(() => {
        if(currentPage) {
            getRoomsRequest.doRequest()
        }
    },[currentPage]);
  return (
    <React.Fragment>
        {getRoomsRequest.loading && <Loading />}
        {getRoomsRequest.errors && <Notification status={'error'} message={getRoomsRequest.errors[0].message} />}
        {updateRoomDataReques.loading && <Loading />}
        {updateRoomDataReques.errors && <Notification status={'error'} message={updateRoomDataReques.errors[0].message} />}
         <div className="profile-container">
        <div className="right-section">
            <>
              <Table2
                headers={[
                  t("room_number"),
                  t("room_fee"),
                  t('max_pat_count'),
                  t('patients_count'),
                  t("remove_pat"),
                ]}
              >
                {rooms.map((u) => (
                  <TableRow
                    key={u.id}
                    sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
                  >
                    <TableCell component="th" scope="row">
                      {u.roomNumber}
                    </TableCell>
                    <TableCell align="right">{u.roomFee}</TableCell>
                    <TableCell align="right">{u.maxPatientsCount}</TableCell>
                    <TableCell align="right">{u.patientsCount}</TableCell>
                    <TableCell align="right">
                      <button
                        className="edit-btn"
                        onClick={() => updateRoomData(u.id)}
                      >
                        {t('remove')}
                      </button>
                    </TableCell>
                  </TableRow>
                ))}
              </Table2>

              <div className="pagination">
                <Pagination
                  className="pagination-bar"
                  currentPage={currentPage}
                  totalCount={count}
                  pageSize={allPages}
                  onPageChange={(page) => changeCurrentPage(page)}
                />
              </div>
            </>
        </div>
      </div>
    </React.Fragment>
  )
}

export default ControlRoom