import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import Loading from '../../components/Loading/Loading';
import Notification from "../../components/Notification/Notification";
import Table from '../../components/Table/Table';
import useRequest from '../../hooks/use-request';
import "./doctor.css"

const Doctor = ({token, user, getUser}) => {
  const { t } = useTranslation();
  const [reservation, setReservation] = useState([]);

  const findReservationsRequest = useRequest({
    url: `/reservation/doctor`,
    method: 'get',
    headers: {
        Authorization: `Bearer ${token}`,
    },
    body: {},
    onSuccess: (data) => {
        if(data.data){
            setReservation(data.data);
        } else {
            setReservation([])
        }
    }
});

const updateReservationRequest = useRequest({
    url: `/reservation/doctor`,
    method: 'put',
    headers: {
        Authorization: `Bearer ${token}`,
    },
    body: {
        isQueue: false
    },
    onSuccess: (data) => {
        findReservationsRequest.doRequest()
    }
});

const updateUserRequest = useRequest({
    url: '/user',
    method: 'put',
    headers: {
        Authorization: `Bearer ${token}`,
    },
    body: {
        soreQueue: 0
    },

    onSuccess: (data) => {
        getUser();
    }
})

useEffect(() => {
    findReservationsRequest.doRequest();
},[]);

const updateReservation = async (id) => {
    if(window.confirm(t("are_you_sure"))) {
    await updateReservationRequest.doRequest(id);
    }
}

const updateUser = async () => {
    await updateUserRequest.doRequest();
}

  return (
    <React.Fragment>
        {findReservationsRequest.loading && <Loading />}
        {updateReservationRequest.loading && <Loading />}
        {updateReservationRequest.errors && <Notification status={'error'} message={updateReservationRequest.errors[0].message} />}
        {updateUserRequest.loading && <Loading />}
        {updateUserRequest.errors && <Notification status={'error'} message={updateUserRequest.errors[0].message} />}
    <div className="doctor-container">
        <div className='right-section'>
            <div className='right-section-header'>
                <div className={'right-section-control active'}>
                    <button>
                       {t("patients")}
                    </button>
                </div>
            </div>

            <div className="control-container">
                <p>{t("queue_length")}: {user.soreQueue}</p>
                <button onClick={updateUser} >{t("reset_queue")}</button>
            </div>

            {reservation.length > 0 && <Table headers={[t("queue"), t("full_name"), t("phone_number"), t("checked")]}>
                {reservation.map(reser => (
                        <tr key={reser.id}>
                            <td data-label={t("queue")}>{reser.queue}</td>
                            <td data-label={t("full_name")}>{reser.sore.name}</td>
                            <td data-label={t("phone_number")}>{reser.sore.phone}</td>
                            <td data-label={t("checked")}>
                                <input type={'checkbox'} onClick={() => updateReservation(reser.id)} />
                            </td>
                        </tr>
                ))}
            </Table>}
        </div>
    </div>
    </React.Fragment>
  )
}

export default Doctor