import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import useRequest from "../../hooks/use-request";
import Table from '../../components/Table/Table';
import Loading from '../../components/Loading/Loading';
import Notification from '../../components/Notification/Notification';
import './history.css'

const History = ({token}) => {
    const {t} = useTranslation();
    const params = useParams();
    const [data, setData] = useState([]);

    const getReservations = useRequest({
        url: `/reservation/${params.id}`,
        method: 'get',
        headers: {
            Authorization: `Bearer ${token}`,
        },
        body: {},
        onSuccess: (data) => {
            setData(data.data)
        }
    });

    useEffect(() => {
        getReservations.doRequest();
    },[])

  return (
    <React.Fragment>
        {getReservations.loading && <Loading />}
        {getReservations.errors && <Notification status={'error'} message={getReservations.errors[0].message} />}
        <div className='category-container'>
      <div className='category-body'>
      <Table headers={[t("profession"),t("consult_fee"),t("queue"),t("doctor") , t("payment_type")]}>
              {data.map(d => (
              <tr key={d.id}>
                  <td data-label={t("professiob")}>{d.type}</td>
                  <td data-label={t("consult_fee")}>{d.fee}</td>
                  <td data-label={t("queue")}>{d.queue}</td>
                  <td data-label={t("doctor")}>{d.doctorName}</td>
                  <td data-label={t("payment_type")}>
                    {d.paymentType && d.paymentType.length > 0 ? d.paymentType.map(ty => (
                        <p key={Math.random()}>{ty == "cash" && t("cash")} {ty == "card" && t("card")} </p>
                    )): "To'lanmagan"}
                  </td>
              </tr>
          ))} 
      </Table>
      </div>
  </div>
    </React.Fragment>
  )
}

export default History