import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import InputMask from "react-input-mask";
import useRequest from "../../hooks/use-request";
import Loading from "../../components/Loading/Loading";
import Notification from "../../components/Notification/Notification";
import Table from "../../components/Table/Table";
import "./patient.css";
import { NavLink } from "react-router-dom";
import Pagination from "../../components/Pagination/Pagination";

const Patient = ({ token }) => {
  const { t } = useTranslation();
  const [data, setData] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [count, setCount] = useState(0);
  const [allPages, setAllPages] = useState(0);
  const [phone, setPhone] = useState(null);

  let url = `/sore?limit=10&page=${currentPage}`;

  if (phone) {
    url = `/sore?phone=${phone}&limit=10&page=${currentPage}`;
  }

  useEffect(() => {
    getPatient.doRequest();
  }, [currentPage]);

  // Get Categories Request
  const getPatient = useRequest({
    url: url,
    method: "get",
    headers: {
      Authorization: `Bearer ${token}`,
    },
    body: {},
    onSuccess: (data) => {
      setCount(data.count);
      setAllPages(data.allPages);
      setData(data.data);
    },
  });

  const changeCurrentPage = (page) => {
    setCurrentPage(page);
  };

  const onSubmitSearch = async (e) => {
    e.preventDefault();
    await getPatient.doRequest();
  };

  return (
    <React.Fragment>
      {getPatient.loading && <Loading />}
      <div className="category-container">
        <div className="category-body">
          <div className="search-section">
            <form onSubmit={onSubmitSearch}>
              <InputMask
                mask={"99-999-99-99"}
                placeholder={"90-999-99-99"}
                onChange={(e) => setPhone(e.target.value)}
                disabled={false}
                maskChar="-"
              />
              <button>{t("search")}</button>
            </form>
          </div>
          <Table
            caption={t("patients_list")}
            headers={[
              t("full_name"),
              t("phone_number"),
              "ID",
              t("application_history"),
            ]}
          >
            {data.map((d) => (
              <tr key={d.id}>
                <td data-label={t("full_name")}>{d.name}</td>
                <td data-label={t("phone_number")}>{d.phone}</td>
                <td data-label="ID">{d.idNumber}</td>
                <td data-label={t("application_history")}>
                  <NavLink to={`/patients/history/${d.id}`}>{t("see")}</NavLink>
                </td>
              </tr>
            ))}
          </Table>
          <div className="pagination">
            <Pagination
              className="pagination-bar"
              currentPage={currentPage}
              totalCount={count}
              pageSize={allPages}
              onPageChange={(page) => changeCurrentPage(page)}
            />
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};

export default Patient;
