import { NavLink } from "react-router-dom";
import { useTranslation } from "react-i18next";
import "./sidebar.css";

const Sidebar = (props) => {
  const { innerWidth: width } = window;
  const { t } = useTranslation();

  const onCloseSideBar = () => {
    if(width < 768) {
      props.setCloseSidebar();
    }
  }

  return (
    <>
      <div
        className={props.isSidebar ? "sidebar-container-open" : ""}
        onClick={() => props.setOpenCloseSideBar()}
      ></div>
      <div className={props.isSidebar ? "sidebar" : "sidebar closed"}>
        <div className={"sidebar-header"}></div>
        <div className={"sidebar-body"}>
          <ul>
            <li>
              <NavLink
                className={({ isActive }) =>
                  isActive ? "link active" : "link"
                }
                to={"/dashboard"}
                onClick={onCloseSideBar}
              >
                {t("dashboard")}
              </NavLink>
            </li>
            {props.role === "superadmin" && (
              <>
                <li>
                  <NavLink className={"link"} to={"/users"} onClick={onCloseSideBar}>
                    {t("users")}
                  </NavLink>
                </li>
                <li>
                  <NavLink className={"link"} to={"/category"} onClick={onCloseSideBar}>
                    {t("category")}
                  </NavLink>
                </li>
                <li>
                  <NavLink className={"link"} to={"/room"} onClick={onCloseSideBar}>
                    {t("rooms")}
                  </NavLink>
                </li>
                <li>
                  <NavLink className={"link"} to={"/patients"} onClick={onCloseSideBar}>
                    {t("patients")}
                  </NavLink>
                </li>
              </>
            )}
            {props.role === "admin" && 
             <li>
             <NavLink className={"link"} to={"/admin/rooms"} onClick={onCloseSideBar}>
                {t("rooms")}
              </NavLink>
           </li>   
            }
            <li>
              <NavLink
                className={({ isActive }) =>
                  isActive ? "link active" : "link"
                }
                to={"/profile"}
                onClick={onCloseSideBar}
              >
                {t("profile")}
              </NavLink>
            </li>
          </ul>
        </div>
      </div>
    </>
  );
};

export default Sidebar;
