import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import Notification from "../../components/Notification/Notification";
import useRequest from "../../hooks/use-request";
import Table from "../../components/Table/Table";

import "./cashier.css";
import Loading from "../../components/Loading/Loading";

const Cashier = ({ token }) => {
  const { t } = useTranslation();
  const [id, setId] = useState(null);
  const [idNumber, setIdNumber] = useState(null);
  const [sore, setSore] = useState(null);
  const [reservation, setReservation] = useState([]);
  const [roomReservation, setRoomReservation] = useState([]);
  const [roomNowPaid, setRoomNowPaid] = useState(null)
  const [card, setCard] = useState(false);
  const [cash, setCash] = useState(false);
  const [paymentType, setPaymentType] = useState([]);
  const [nowPaid, setNowPaid] = useState(null);
  const [isRoom, setIsRoom] = useState(false);

  useEffect(() => {
    if (card || cash) {
      if (card && cash) {
        setPaymentType(["card", "cash"]);
      } else if (card && !cash) {
        setPaymentType(["card"]);
      } else if (cash && !card) {
        setPaymentType(["cash"]);
      }
    }
  }, [card, cash]);

  const findUserRequest = useRequest({
    url: `/sore/cachier?idNumber=${idNumber}`,
    method: "get",
    headers: {
      Authorization: `Bearer ${token}`,
    },
    body: {},
    onSuccess: (data) => {
      if(data.data.roomReservation) {
        setRoomReservation(data.data.roomReservation)
      }
      setSore(data.data.sore);
      if (data.data.sore.reservation) {
        setReservation(data.data.sore.reservation);
      } else {
        setReservation([]);
      }
      setId(data.data.sore.id);
    },
  });

  const updateSoreRequest = useRequest({
    url: `/reservation/${id}`,
    method: "put",
    headers: {
      Authorization: `Bearer ${token}`,
    },
    body: {
      paymentType,
      isPaid: true,
      isQueue: true,
    },
    onSuccess: (data) => {
      setNowPaid(data.data);
      setReservation((res) => res.id !== data.data.id);
    },
  });

  const updateSoreForRoomRequest = useRequest({
    url: `/reservation/room/${id}`,
    method: "put",
    headers: {
      Authorization: `Bearer ${token}`,
    },
    body: {
      paymentType,
      isPaid: true,
    },
    onSuccess: (data) => {
      setRoomNowPaid(data.data);
      setRoomReservation((res) => res.id !== data.data.id);
    },
  });

  const onSubmitData = async (e) => {
    e.preventDefault();
    await findUserRequest.doRequest();
  };

  const onSubmitUpdateData = async (e) => {
    e.preventDefault();
    if(isRoom) {
      if (window.confirm(t('are_you_sure'))) {
        await updateSoreForRoomRequest.doRequest();
      }
    } else {
      if (window.confirm(t('are_you_sure'))) {
        await updateSoreRequest.doRequest();
      }
    }
  };

  return (
    <React.Fragment>
      {findUserRequest.loading && <Loading />}
      {findUserRequest.errors && (
        <Notification
          status={"warning"}
          message={findUserRequest.errors[0].message}
        />
      )}
      {updateSoreRequest.loading && <Loading />}
      {updateSoreRequest.errors && (
        <Notification
          status={"error"}
          message={updateSoreRequest.errors[0].message}
        />
      )}
      {updateSoreForRoomRequest.loading && <Loading />}
      {updateSoreForRoomRequest.errors && (
        <Notification
        status={"error"}
        message={updateSoreForRoomRequest.errors[0].message}
      />
      )}
      <div className="cashier-container">
        <div className="search-section">
          <form onSubmit={onSubmitData}>
            <input
              type={"number"}
              required={true}
              placeholder={t("inp_ptn_id")}
              onChange={(e) => setIdNumber(e.target.value)}
            />
            <button>{t("search")}</button>
          </form>
        </div>
        {sore && (
          <>
            <div style={{ marginTop: "20px" }}>
              <Table headers={[t("full_name"), t("phone_number"), "ID"]}>
                <tr>
                  <td scope="row" data-label={t("full_name")}>
                    {sore.name}
                  </td>
                  <td data-label={t("phone_number")}>{sore.phone}</td>
                  <td data-label="ID">{sore.idNumber}</td>
                </tr>
              </Table>
              {nowPaid && (
                <>
                  <p
                    style={{ margin: "10px", color: "#000", fontSize: "14px" }}
                  >
                    {t("now_paid")}
                  </p>
                  <Table
                    headers={[
                      t("profession"),
                      t("consult_fee"),
                      t("doctor"),
                      t("room"),
                      t("queue"),
                    ]}
                  >
                    <tr key={nowPaid.id}>
                      <td scope="row" data-label={t("profession")}>
                        {nowPaid.type}
                      </td>
                      <td data-label={t("consult_fee")}>{nowPaid.fee}</td>
                      <td data-label={t("doctor")}>{nowPaid.doctorName}</td>
                      <td data-label={t("room")}>{nowPaid.room}</td>
                      <td data-label={t("queue")}>{nowPaid.queue}</td>
                    </tr>
                  </Table>
                </>
              )}
                {roomNowPaid && (
                <>
                  <p
                    style={{ margin: "10px", color: "#000", fontSize: "14px" }}
                  >
                    {t("now_paid")}
                  </p>
                  <Table
                    headers={[
                      t("room"),
                      t("fee"),
                      t("isPaid"),
                    ]}
                  >
                    <tr key={roomNowPaid.id}>
                      <td scope="row" data-label={t("room")}>
                        {roomNowPaid.roomNumber}
                      </td>
                      <td data-label={t("fee")}>{roomNowPaid.fee}</td>
                      <td data-label={t("isPaid")}>{roomNowPaid.isPaid ? t("confirmed"): t("unconfirmed")}</td>
                    </tr>
                  </Table>
                </>
              )}
              {reservation.length > 0 && (
                <>
                  <p
                    style={{ margin: "10px", color: "#000", fontSize: "14px" }}
                  >
                    {t("application")}
                  </p>
                  <Table
                    headers={[
                      t("profession"),
                      t("consult_fee"),
                      t("doctor"),
                      t("room"),
                      t("pay"),
                    ]}
                  >
                    {reservation.map((r) => (
                      <tr key={r.id}>
                        <td scope="row" data-label={t("profession")}>
                          {r.type}
                        </td>
                        <td data-label={t("consult_fee")}>{r.fee}</td>
                        <td data-label={t("doctor")}>{r.doctorName}</td>
                        <td data-label={t("room")}>{r.room}</td>
                        <td data-label={t("pay")}>
                          <div className="paid-container">
                            <form onSubmit={onSubmitUpdateData}>
                              <div>
                                <div className="label">
                                  <label>{t("card")}</label>
                                  <input
                                    type={"checkbox"}
                                    value={card}
                                    onChange={(e) => {setCard(!card); setIsRoom(false)}}
                                  />
                                </div>
                                <div className="label">
                                  <label>{t("cash")}</label>
                                  <input
                                    type={"checkbox"}
                                    value={cash}
                                    onChange={(e) => {setCash(!cash); setIsRoom(false)}}
                                  />
                                </div>
                              </div>
                              <button onClick={() => setId(r.id)}>
                                {t("save")}
                              </button>
                            </form>
                          </div>
                        </td>
                      </tr>
                    ))}
                  </Table>
                </>
              )}
              {
                roomReservation.length > 0 && (
                  <>
                  <p
                    style={{ margin: "10px", color: "#000", fontSize: "14px" }}
                  >
                    {t("application")}
                  </p>
                  <Table
                    headers={[
                      t("room"),
                      t("fee"),
                      t("pay"),
                    ]}
                  >
                    {roomReservation.map((r) => (
                      <tr key={r.id}>
                        <td scope="row" data-label={t("room")}>
                          {r.roomNumber}
                        </td>
                        <td data-label={t("fee")}>{r.fee}</td>
                        <td data-label={t("pay")}>
                          <div className="paid-container">
                            <form onSubmit={onSubmitUpdateData}>
                              <div>
                                <div className="label">
                                  <label>{t("card")}</label>
                                  <input
                                    type={"checkbox"}
                                    value={card}
                                    onChange={(e) => {setCard(!card); setIsRoom(true)}}
                                  />
                                </div>
                                <div className="label">
                                  <label>{t("cash")}</label>
                                  <input
                                    type={"checkbox"}
                                    value={cash}
                                    onChange={(e) => {setCash(!cash); setIsRoom(true)}}
                                  />
                                </div>
                              </div>
                              <button onClick={() => setId(r.id)}>
                                {t("save")}
                              </button>
                            </form>
                          </div>
                        </td>
                      </tr>
                    ))}
                  </Table>
                </>
                )
              }
            </div>
          </>
        )}
      </div>
    </React.Fragment>
  );
};

export default Cashier;
