import "./table.css";

const Table = (props) => {
    return <div className="table-container"> <table>
        {props.caption && (
            <caption>{props.caption}</caption>
        )}
        <thead>
            <tr>
                {props.headers.map(head => (
                    <th scope="col" key={head}>{head}</th>
                ))}
            </tr>
        </thead>
        <tbody>
            {props.children}
        </tbody>
    </table></div>
}

export default Table;