import { FormControl, InputLabel } from "@mui/material";
import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import Loading from "../../components/Loading/Loading";
import Notification from "../../components/Notification/Notification";
import useRequest from "../../hooks/use-request";
import "./profile.css";

const Profile = ({ user, token, getUser }) => {
  const { t } = useTranslation();
  const [editPassword, setEditPassword] = useState(false);
  const [name, setName] = useState(user.name);
  const [role, setRole] = useState(user.role);
  const [birthday, setBirthday] = useState(user.birthday);
  const [degree, setDegree] = useState(user.degree);
  const [login, setLogin] = useState(user.login);
  const [workExperience, setWorkExperience] = useState(user.workExperience);
  const [profession, setProfession] = useState(user.profession);
  const [consultationFee, setConsultationFee] = useState(user.consultationFee);
  const [salary, setSalary] = useState(user.salary);
  const [room, setRoom] = useState(user.room);
  const [category, setCategory] = useState([]);
  const [image, setImage] = useState(user.image);
  const [editImage, setEditImage] = useState("");
  const [currentPassword, setCurrentPassword] = useState("");
  const [password, setPassword] = useState("");
  const [reapetPassword, setReapetPassword] = useState("");
  const [passErr, setPassErr] = useState(null);

  const userData = new FormData();

  const getCategory = useRequest({
    url: "/category",
    method: "get",
    headers: {
      Authorization: `Bearer ${token}`,
    },
    body: {},
    onSuccess: (data) => {
      setCategory(data.data);
    },
  });

  const updateUserDetails = useRequest({
    url: "/user",
    method: "put",
    headers: {
      Authorization: `Bearer ${token}`,
    },
    body: userData,
    onSuccess: (data) => {
      getUser();
    },
  });

  const updateUserPassword = useRequest({
    url: "/user",
    method: "put",
    headers: {
      Authorization: `Bearer ${token}`,
    },
    body: {
      currentPassword,
      password,
    },
    onSuccess: (data) => {
      setCurrentPassword("");
      setPassword("");
      setReapetPassword("");
    },
  });

  useEffect(() => {
    getCategory.doRequest();
  }, []);

  const onSubmitDetails = async (e) => {
    e.preventDefault();
    if (role === "superadmin") {
      userData.append("name", name);
      userData.append("role", role);
      userData.append("birthday", birthday);
      userData.append("degree", degree);
      userData.append("login", login);
      userData.append("workExperience", workExperience);
      userData.append("profession", profession);
      userData.append("consultationFee", consultationFee);
      userData.append("room", room);
      userData.append("salary", salary);
      if (editImage) {
        userData.append("image", editImage);
      }
    } else {
      userData.append("login", login);
      if (editImage) {
        userData.append("image", editImage);
      }
    }

    await updateUserDetails.doRequest();
  };

  const onSubmitChangePassword = async (e) => {
    e.preventDefault();
    if (password !== reapetPassword) {
      setPassErr("Passwords do'nt mutch");
    } else {
      await updateUserPassword.doRequest();
    }
  };

  return (
    <React.Fragment>
      {updateUserDetails.loading && <Loading />}
      {updateUserPassword.loading && <Loading />}
      {passErr && <Notification status={"error"} message={passErr} />}
      {updateUserDetails.errors && (
        <Notification
          status={"error"}
          message={updateUserDetails.errors[0].message}
        />
      )}
      {updateUserPassword.errors && (
        <Notification
          status={"error"}
          message={updateUserPassword.errors[0].message}
        />
      )}
      <div className="profile-container">
        <div className="left-section">
          <div className="image">
            <img
              crossOrigin="anonymous"
              src={
                image
                  ? `https://xalilov-project.online/${image}`
                  : "/assets/no-photo.png"
              }
              alt="rasm"
            />
          </div>
          <div className="info">
            <p className="name">{name}</p>
            <p className="role">{role}</p>
          </div>
        </div>
        <div className="right-section">
          <div className="right-section-header">
            <div
              className={
                editPassword
                  ? "right-section-control"
                  : "right-section-control active"
              }
            >
              <button onClick={() => setEditPassword(false)}>
                {t("personal_details")}
              </button>
            </div>
            <div
              className={
                !editPassword
                  ? "right-section-control"
                  : "right-section-control active"
              }
            >
              <button onClick={() => setEditPassword(true)}>
                {t("change_password")}
              </button>
            </div>
          </div>
          {!editPassword ? (
            <div className="right-section-details">
              <form onSubmit={onSubmitDetails}>
                <div className="detail">
                  <label> {t("full_name")}</label>
                  <input
                    type={"text"}
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                    disabled={role !== "superadmin" ? true : false}
                    required
                  />
                </div>
                <div className="detail">
                  <label>{t("username")}</label>
                  <input
                    type={"text"}
                    value={login}
                    onChange={(e) => setLogin(e.target.value)}
                    required
                  />
                </div>
                <div className="detail">
                  <label>{t("birthday")}</label>
                  <input
                    type={"date"}
                    value={birthday}
                    onChange={(e) => setBirthday(e.target.value)}
                    required
                  />
                </div>
                <div className="detail">
                  <label>{t("degree")}</label>
                  <input
                    type={"text"}
                    value={degree}
                    onChange={(e) => setDegree(e.target.value)}
                    required
                  />
                </div>
                <div className="detail">
                  <label>{t("work_exp")}</label>
                  <input
                    type={"text"}
                    value={workExperience}
                    onChange={(e) => setWorkExperience(e.target.value)}
                    required
                  />
                </div>
                <div className="detail">
                  <label>{t("select_prof")}</label>
                  <select
                    value={profession}
                    onChange={(e) => setProfession(e.target.value)}
                    required={true}
                    disabled={role !== "superadmin" ? true : false}
                  >
                    <option>{t("select_prof")}</option>
                    {category.map((c) => (
                      <option key={c.id} value={c.slug}>
                        {c.category}
                      </option>
                    ))}
                  </select>
                </div>
                <div className="detail">
                  <label>{t("consult_fee")} (so'm)</label>
                  <input
                    type={"number"}
                    value={consultationFee}
                    onChange={(e) => setConsultationFee(e.target.value)}
                    disabled={role !== "superadmin" ? true : false}
                    required
                  />
                </div>
                <div className="detail">
                  <label>{t("salary")} (so'm)</label>
                  <input
                    type={"text"}
                    value={salary}
                    onChange={(e) => setSalary(e.target.value)}
                    disabled={role !== "superadmin" ? true : false}
                    required
                  />
                </div>
                <div className="detail">
                  <label>{t("room")}</label>
                  <input
                    type={"text"}
                    value={room}
                    onChange={(e) => setRoom(e.target.value)}
                    disabled={role !== "superadmin" ? true : false}
                    required
                  />
                </div>
                <div className="detail">
                  <label>{t("select_img")}</label>
                  <input
                    type="file"
                    onChange={(e) => setEditImage(e.target.files[0])}
                  />
                </div>
                <div className="update-button">
                  <button>{t("update")}</button>
                </div>
              </form>
            </div>
          ) : (
            <div className="right-section-details">
              <form onSubmit={onSubmitChangePassword}>
                <div className="detail">
                  <label>{t("curr_pass")}</label>
                  <input
                    type={"password"}
                    value={currentPassword}
                    onChange={(e) => setCurrentPassword(String(e.target.value))}
                    required
                  />
                </div>
                <div className="detail">
                  <label>{t("new_pass")}</label>
                  <input
                    type={"password"}
                    value={password}
                    onChange={(e) => setPassword(String(e.target.value))}
                    required
                  />
                </div>
                <div className="detail">
                  <label>{t("reap_new_pass")}</label>
                  <input
                    type={"password"}
                    value={reapetPassword}
                    onChange={(e) => setReapetPassword(String(e.target.value))}
                    required
                  />
                </div>
                <div className="update-button">
                  <button>{t("update")}</button>
                </div>
              </form>
            </div>
          )}
        </div>
      </div>
    </React.Fragment>
  );
};

export default Profile;
