import React, { useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import Notification from '../../components/Notification/Notification'
import useRequest from '../../hooks/use-request';
import Loading from '../../components/Loading/Loading';
import Table from '../../components/Table/Table';
import './category.css';

const Category = ({token}) => {
  const { t } = useTranslation();
  const [data, setData] = useState([]);
  const [category, setCategory] = useState('');

  useEffect(() => {
    getCategories.doRequest();
  }, []);

  // Get Categories Request
  const getCategories = useRequest({
    url: '/category',
    method: 'get',
    headers: {
      Authorization: `Bearer ${token}`,
    },
    body: {},
    onSuccess: (data) => {
      setData(data.data);
    }
  })

  const createCategory = useRequest({
    url: '/category',
    method: 'post',
    headers: {
      Authorization: `Bearer ${token}`,
    },
    body: {category},
    onSuccess: (data) => {
      setCategory('');
      getCategories.doRequest();
    }
  });

  const deleteCategoryRequest = useRequest({
    url: `/category`,
    method: 'delete',
    headers: {
      Authorization: `Bearer ${token}`
    },
    body: {},
    onSuccess: () => {
      getCategories.doRequest();
    }
  })

  const submitCategory = async (e) => {
    e.preventDefault();
    await createCategory.doRequest();
  }

  const deleteCategory = async (id) => {
    if(window.confirm("Do you want to delete")) {
       deleteCategoryRequest.doRequest(id);
    }
  }

  // console.log(createCategory.errors);

    return(
      <React.Fragment>
          {getCategories.loading && <Loading />}
          {deleteCategoryRequest.loading && <Loading />}
          {deleteCategoryRequest.errors && <Notification message={deleteCategoryRequest.errors[0].message} />}
          {createCategory.errors && <Notification status={'error'} message={createCategory.errors[0].message} />}
        <div className='category-container'>
            <div className='category-header'>
                <form onSubmit={submitCategory}>
                        <div className={"label"}>
                            <input type={'text'} value={category} onChange={(e) => setCategory(e.target.value)} required={true} />
                            <button> + {t("add_category")}</button>
                        </div>
                </form>
            </div>
            <div className='category-body'>
              {data && data.length > 0 ? 
                     <Table caption={t("category_list")} headers={[t("name"),"Slug", t("delete")]}>
                     {data.map(d => (
                     <tr key={d.id}>
                         <td data-label={t("name")}>{d.category}</td>
                         <td data-label="Slug">{d.slug}</td>
                         <td data-label={t("delete")}>
                         <button className="delete-btn" onClick={() => {deleteCategory(d.id)}}>
                             <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" className="bi bi-trash" viewBox="0 0 16 16">
                               <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z"/>
                               <path fillRule="evenodd" d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"/>
                             </svg>
                         </button>
                         </td>
                     </tr>
                 ))} 
             </Table> : ""
            }
            </div>
        </div>
      </React.Fragment>  
    )
}

export default Category;