import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import useRequest from "../../hooks/use-request";
import Loading from "../../components/Loading/Loading";
import "./dashboard.css";

// Chart
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend,
} from "chart.js";
import { Bar } from "react-chartjs-2";

ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
);

const Dashboard = ({ token }) => {
  const { t } = useTranslation();
  const [workers, setWorkers] = useState("");
  const [forWorkers, setForWorkers] = useState("");
  const [balance, setBalance] = useState("");
  const [forBalance, setForBalance] = useState("ming");
  const [patients, setPatients] = useState("");
  const [forPatients, setForPatients] = useState("");
  const [labels, setLabels] = useState([]);
  const [chartData, setChartData] = useState([]);

  const options = {
    responsive: true,
    plugins: {
      legend: {
        position: "top",
      },
      title: {
        display: true,
        text: t("with_category"),
      },
    },
  };

  const data = {
    labels,
    datasets: [
      {
        label: t("patients"),
        data: chartData,
        backgroundColor: "rgba(53, 162, 235, 0.5)",
      },
    ],
  };

  const getStatisctics = useRequest({
    url: "/reservation/statistics",
    method: "get",
    headers: {
      Authorization: `Bearer ${token}`,
    },
    body: {},
    onSuccess: (data) => {
      const stat = data.data.statistic;
      setLabels(data.data.categories);
      setChartData(data.data.catDatas);
      if (stat.users < 1000) {
        setWorkers(stat.users);
      } else if (stat.users < 1000000) {
        setWorkers(stat.users / 1000);
        setForWorkers("ming");
      } else if (stat.users < 1000000000) {
        setWorkers(stat.users / 1000000);
        setForWorkers("million");
      }
      if (stat.balance < 1000000) {
        setBalance(stat.balance / 1000);
      } else if (stat.balance < 1000000000) {
        setBalance(stat.balance / 1000000);
        setForBalance("mln");
      } else if (stat.balance < 1000000000000) {
        setBalance(stat.balance / 1000000000);
        setForBalance("mlrd");
      } else if (stat.balance < 1000000000000000) {
        setBalance(stat.balance / 1000000000000);
        setForBalance("trln");
      }

      if (stat.patients < 1000) {
        setPatients(stat.patients);
      } else if (stat.patients < 1000000) {
        setPatients(stat.patients / 1000);
        setForPatients("ming");
      } else if (stat.patients < 1000000000) {
        setPatients(stat.patients / 1000000);
        setForPatients("mln");
      } else if (stat.patients < 1000000000000) {
        setPatients(stat.patients / 1000000000);
        setForPatients("mlrd");
      } else if (stat.patients < 1000000000000000) {
        setPatients(stat.patients / 1000000000000);
        setForPatients("trln");
      }
    },
  });

  useEffect(() => {
    getStatisctics.doRequest();
  }, []);

  return (
    <React.Fragment>
      {getStatisctics.loading && <Loading />}
      <div className="dashboard-container">
        {getStatisctics.loading && <Loading />}
        <div className="general">
          <div className="card">
            <div className="card-header">
              <p>
                {t("worker")} <small>({forWorkers})</small>
              </p>
            </div>
            <div className="card-body">
              <h1>{workers}</h1>
            </div>
          </div>
          <div className="card">
            <div className="card-header">
              <p>
                {t("total_amount")} <small>({forBalance})</small>
              </p>
            </div>
            <div className="card-body">
              <h1>{balance}</h1>
            </div>
          </div>
          <div className="card">
            <div className="card-header">
              <p>
                {t("patients")} <small>({forPatients})</small>
              </p>
            </div>
            <div className="card-body">
              <h1>{patients}</h1>
            </div>
          </div>
        </div>
        <div className="diagrams">
          <Bar options={options} data={data} />
        </div>
      </div>
    </React.Fragment>
  );
};

export default Dashboard;
