import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import useRequest from "../../hooks/use-request";
import Pagination from "../../components/Pagination/Pagination";
import { TableRow, TableCell } from "@mui/material";
import Loading from "../../components/Loading/Loading";
import Notification from "../../components/Notification/Notification";
import "./room.css";
import Table2 from "../../components/Table/Table2";

const Room = ({ token }) => {
  const { t } = useTranslation();
  const [createRoom, setCreateRoom] = useState(false);
  const [updateRoom, setUpdateRoom] = useState(false);
  const [roomNumber, setRoomNumber] = useState("");
  const [maxPatientsCount, setMaxPatientsCount] = useState("");
  const [roomFee, setRoomFee] = useState("");
  const [rooms, setRooms] = useState([]);
  const [sort, setSort] = useState("");
  const [id, setId] = useState(null);
  const [currentPage, setCurrentPage] = useState(1);
  const [count, setCount] = useState(0);
  const [allPages, setAllPages] = useState(0);

  const createRoomRequest = useRequest({
    url: "/room",
    method: "post",
    headers: {
      Authorization: `Bearer ${token}`,
    },
    body: {
      roomNumber,
      roomFee,
      maxPatientsCount
    },
    onSuccess: (data) => {
      getRoomsRequest.doRequest();
    },
  });

  const getRoomsRequest = useRequest({
    url: `/room?limit=10&page=${currentPage}`,
    method: "get",
    headers: {
      Authorization: `Bearer ${token}`,
    },
    body: {},
    onSuccess: (data) => {
      setCount(data.count);
      setAllPages(data.allPages);
      setRooms(data.data);
    },
  });

  const updateRoomRequest = useRequest({
    url: `/room/${id}`,
    method: "put",
    headers: {
      Authorization: `Bearer ${token}`,
    },
    body: {
      roomFee,
      roomNumber,
    },
    onSuccess: (data) => {
      getRoomsRequest.doRequest();
    },
  });

  const deleteRoomRequest = useRequest({
    url: `/room`,
    method: "delete",
    headers: {
      Authorization: `Bearer ${token}`,
    },
    body: {},
    onSuccess: (data) => {
      getRoomsRequest.doRequest();
    },
  });

  const deleteRoom = async (id) => {
    if (window.confirm(t("are_you_sure"))) {
      await deleteRoomRequest.doRequest(id);
    }
  };

  const updateRoomData = (id) => {
    setId(id);
    rooms.find((room) => {
      if (room.id === id) {
        setRoomFee(room.roomFee);
        setRoomNumber(room.roomNumber);
        setMaxPatientsCount(room.maxPatientsCount)
      }
    });
    setUpdateRoom(true);
    setCreateRoom(true);
  };

  useEffect(() => {
    if(currentPage) {
      getRoomsRequest.doRequest();
    }
  }, [currentPage]);

  const onSubmitRoomData = async (e) => {
    e.preventDefault();
    if (!updateRoom) {
      await createRoomRequest.doRequest();
    } else {
      await updateRoomRequest.doRequest();
    }
  };

  const changeCurrentPage = async (page) => {
    setCurrentPage(page);
  };

  return (
    <React.Fragment>
      {getRoomsRequest.loading && <Loading />}
      {createRoomRequest.loading && <Loading />}
      {createRoomRequest.errors && (
        <Notification
          status={"error"}
          message={createRoomRequest.errors[0].message}
        />
      )}
      {updateRoomRequest.loading && <Loading />}
      {updateRoomRequest.errors && <Notification status={'error'} message={updateRoomRequest.errors[0].message} />}
      {deleteRoomRequest.loading && <Loading />}
      {deleteRoomRequest.errors && <Notification status={'error'} message={deleteRoomRequest.errors[0].message} />}
      <div className="profile-container">
        <div className="right-section">
          <div className="right-section-header">
            <div
              className={
                createRoom
                  ? "right-section-control"
                  : "right-section-control active"
              }
            >
              <button
                onClick={() => {
                  setCreateRoom(false);
                  setUpdateRoom(false);
                  getRoomsRequest.doRequest();
                }}
              >
                {t("rooms_list")}
              </button>
            </div>
            <div
              className={
                !createRoom
                  ? "right-section-control"
                  : "right-section-control active"
              }
            >
              <button onClick={() => setCreateRoom(true)}>
                {updateRoom ? t("edit_room") : t("add_room")}
              </button>
            </div>
          </div>
          {!createRoom ? (
            <>
              <Table2
                headers={[
                  t("room_number"),
                  t("room_fee"),
                  t('max_pat_count'),
                  t('patients_count'),
                  t("is_full_room"),
                  t("edit"),
                  t("delete"),
                ]}
              >
                {rooms.map((u) => (
                  <TableRow
                    key={u.id}
                    sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
                  >
                    <TableCell component="th" scope="row">
                      {u.roomNumber}
                    </TableCell>
                    <TableCell align="right">{u.roomFee}</TableCell>
                    <TableCell align="right">{u.maxPatientsCount}</TableCell>
                    <TableCell align="right">{u.patientsCount}</TableCell>
                    <TableCell align="right">
                      {u.isFull ? t("room_is_busy") : t("room_is_free")}
                    </TableCell>
                    <TableCell align="right">
                      <button
                        className="edit-btn"
                        onClick={() => updateRoomData(u.id)}
                      >
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          width="16"
                          height="16"
                          fill="currentColor"
                          className="bi bi-pencil"
                          viewBox="0 0 16 16"
                        >
                          <path d="M12.146.146a.5.5 0 0 1 .708 0l3 3a.5.5 0 0 1 0 .708l-10 10a.5.5 0 0 1-.168.11l-5 2a.5.5 0 0 1-.65-.65l2-5a.5.5 0 0 1 .11-.168l10-10zM11.207 2.5 13.5 4.793 14.793 3.5 12.5 1.207 11.207 2.5zm1.586 3L10.5 3.207 4 9.707V10h.5a.5.5 0 0 1 .5.5v.5h.5a.5.5 0 0 1 .5.5v.5h.293l6.5-6.5zm-9.761 5.175-.106.106-1.528 3.821 3.821-1.528.106-.106A.5.5 0 0 1 5 12.5V12h-.5a.5.5 0 0 1-.5-.5V11h-.5a.5.5 0 0 1-.468-.325z" />
                        </svg>
                      </button>
                    </TableCell>
                    <TableCell align="right">
                      <button
                        className="delete-btn"
                        onClick={() => deleteRoom(u.id)}
                        style={{ marginLeft: "20px" }}
                      >
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          width="16"
                          height="16"
                          fill="currentColor"
                          className="bi bi-trash"
                          viewBox="0 0 16 16"
                        >
                          <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z" />
                          <path
                            fillRule="evenodd"
                            d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"
                          />
                        </svg>
                      </button>
                    </TableCell>
                  </TableRow>
                ))}
              </Table2>

              <div className="pagination">
                <Pagination
                  className="pagination-bar"
                  currentPage={currentPage}
                  totalCount={count}
                  pageSize={allPages}
                  onPageChange={(page) => changeCurrentPage(page)}
                />
              </div>
            </>
          ) : (
            <div className="right-section-details">
              <form onSubmit={onSubmitRoomData}>
                <div className="detail">
                  <label>{t("room_number")}</label>
                  <input
                    type={"text"}
                    value={roomNumber}
                    onChange={(e) => setRoomNumber(e.target.value)}
                    required={true}
                  />
                </div>
                <div className="detail">
                  <label>{t("room_fee")}(So'm)</label>
                  <input
                    type={"number"}
                    value={roomFee}
                    onChange={(e) => setRoomFee(e.target.value)}
                    required={true}
                  />
                </div>
                <div className="detail">
                  <label>{t("max_pat_count")}</label>
                  <input
                    type={"number"}
                    value={maxPatientsCount}
                    onChange={(e) => setMaxPatientsCount(e.target.value)}
                    required={true}
                  />
                </div>
                <div className="update-button">
                  <button>{updateRoom ? t("update") : t("create")}</button>
                </div>
              </form>
            </div>
          )}
        </div>
      </div>
    </React.Fragment>
  );
};

export default Room;
