import { TableCell, TableRow } from "@mui/material";
import React, { useState, useEffect } from "react";
import { useTranslation } from "react-i18next";
import Notification from "../../components/Notification/Notification";
import Loading from "../../components/Loading/Loading";
import Table2 from "../../components/Table/Table2";
import useRequest from "../../hooks/use-request";
import Pagination from "../../components/Pagination/Pagination";
import "./user.css";

const User = ({ token }) => {
  const { t } = useTranslation();
  const [createUser, setCreateUser] = useState(false);
  const [name, setName] = useState("");
  const [role, setRole] = useState("");
  const [birthday, setBirthday] = useState("");
  const [degree, setDegree] = useState("");
  const [login, setLogin] = useState("");
  const [workExperience, setWorkExperience] = useState("");
  const [profession, setProfession] = useState("");
  const [consultationFee, setConsultationFee] = useState("");
  const [salary, setSalary] = useState("");
  const [room, setRoom] = useState("");
  const [editImage, setEditImage] = useState("");
  const [password, setPassword] = useState("");
  const [users, setUsers] = useState([]);
  const [category, setCategory] = useState([]);
  const [updateUser, setUpdateUser] = useState(false);
  const [id, setId] = useState("");
  const [sort, setSort] = useState("");
  const [sortRole, setSortRole] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const [count, setCount] = useState(0);
  const [allPages, setAllPages] = useState(0);

  const userData = new FormData();

  let url = `/user?limit=10&page=${currentPage}`;

  if (sort && sort !== "all") {
    url = `/user?profession=${sort}&limit=10&page=${currentPage}`;
  }

  if (sort && sort !== "all" && sortRole && sortRole !== "all") {
    url = `/user?profession=${sort}&role=${sortRole}&limit=10&page=${currentPage}`;
  } else if (sortRole && sortRole !== "all") {
    url = `/user?role=${sortRole}&limit=10&page=${currentPage}`;
  }

  const getUser = useRequest({
    url: url,
    method: "get",
    headers: {
      Authorization: `Bearer ${token}`,
    },
    body: {},
    onSuccess: (data) => {
      setCount(data.count);
      setAllPages(data.allPages);
      setUsers(data.data);
    },
  });

  const getCategory = useRequest({
    url: "/category",
    method: "get",
    headers: {
      Authorization: `Bearer ${token}`,
    },
    body: {},
    onSuccess: (data) => {
      setCategory(data.data);
    },
  });

  const updateUserRequest = useRequest({
    url: `/user/${id}`,
    method: "put",
    headers: {
      Authorization: `Bearer ${token}`,
    },
    body: userData,
    onSuccess: (data) => {
      getUser.doRequest();
    },
  });

  const deleteUserRequest = useRequest({
    url: `/user`,
    method: "delete",
    headers: {
      Authorization: `Bearer ${token}`,
    },
    body: {
      name,
    },
    onSuccess: (data) => {
      getUser.doRequest();
    },
  });

  const updateUserData = (id) => {
    setId(id);
    users.find((user) => {
      if (user.id === id) {
        setName(user.name);
        setLogin(user.login);
        setBirthday(user.birthday);
        setDegree(user.degree);
        setWorkExperience(user.workExperience);
        setProfession(user.profession);
        setConsultationFee(user.consultationFee);
        setSalary(user.salary);
        setRoom(user.room);
        setRole(user.role);
      }
    });
    setUpdateUser(true);
    setCreateUser(true);
  };

  useEffect(() => {
    getUser.doRequest();
    getCategory.doRequest();
  }, [sort, sortRole, currentPage]);

  const addUser = useRequest({
    url: "/user",
    method: "post",
    headers: {
      Authorization: `Bearer ${token}`,
    },
    body: userData,
    onSuccess: (data) => {
      getUser.doRequest();
      setCreateUser(false);
    },
  });

  const onSubmitUserData = async (e) => {
    e.preventDefault();
    userData.append("name", name);
    userData.append("login", login);
    userData.append("birthday", birthday);
    userData.append("degree", degree);
    userData.append("workExperience", workExperience);
    userData.append("profession", profession);
    userData.append("consultationFee", consultationFee);
    userData.append("salary", salary);
    userData.append("room", room);
    userData.append("role", role);

    if (editImage) {
      userData.append("image", editImage);
    }
    if (password.length !== 0) {
      userData.append("password", password);
    }
    if (!updateUser) {
      await addUser.doRequest();
    } else {
      await updateUserRequest.doRequest();
    }
  };

  const deleteUser = async (id) => {
    if (window.confirm(t("are_you_sure"))) {
      await deleteUserRequest.doRequest(id);
    }
  };

  const changeCurrentPage = (page) => {
    setCurrentPage(page);
  };

  return (
    <React.Fragment>
      {addUser.loading && <Loading />}
      {getUser.loading && <Loading />}
      {updateUserRequest.loading && <Loading />}
      {addUser.errors && (
        <Notification status={"error"} message={addUser.errors[0].message} />
      )}
      {updateUserRequest.errors && (
        <Notification
          status={"error"}
          message={updateUserRequest.errors[0].message}
        />
      )}
      {deleteUserRequest.loading && <Loading />}
      {deleteUserRequest.errors && (
        <Notification
          status={"error"}
          message={deleteUserRequest.errors[0].message}
        />
      )}
      <div className="profile-container">
        <div className="right-section">
          <div className="right-section-header">
            <div
              className={
                createUser
                  ? "right-section-control"
                  : "right-section-control active"
              }
            >
              <button
                onClick={() => {
                  setCreateUser(false);
                  setUpdateUser(false);
                  getUser.doRequest();
                }}
              >
                {t("users_list")}
              </button>
            </div>
            <div
              className={
                !createUser
                  ? "right-section-control"
                  : "right-section-control active"
              }
            >
              <button onClick={() => setCreateUser(true)}>
                {updateUser ? t("edit_user") : t("add_user")}
              </button>
            </div>
          </div>
          {!createUser ? (
            <>
              <div className="sorting-container">
                <div className="sorting">
                  <select
                    value={sort}
                    onChange={(e) => setSort(e.target.value)}
                    required={true}
                  >
                    <option value={"all"}>{t("all")}</option>
                    {category.map((c) => (
                      <option key={c.id} value={c.slug}>
                        {c.category}
                      </option>
                    ))}
                  </select>
                  <select
                    value={sortRole}
                    onChange={(e) => setSortRole(e.target.value)}
                    required={true}
                  >
                    <option value={"all"}>{t("all")}</option>
                    <option value={"admin"}>{t("admin")}</option>
                    <option value={"cachier"}>{t("kasser")}</option>
                  </select>
                </div>
              </div>

              <Table2
                headers={[
                  t("full_name"),
                  t("username"),
                  t("birthday"),
                  t("degree"),
                  t("work_exp"),
                  t("profession"),
                  t("consult_fee"),
                  t("salary"),
                  t("room"),
                  t("image"),
                  t("edit"),
                  t("delete"),
                ]}
              >
                {users.map((u) => (
                  <TableRow
                    key={u.id}
                    sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
                  >
                    <TableCell component="th" scope="row">
                      {u.name}
                    </TableCell>
                    <TableCell align="right">{u.login}</TableCell>
                    <TableCell align="right">{u.birthday}</TableCell>
                    <TableCell align="right">{u.degree}</TableCell>
                    <TableCell align="right">{u.workExperience}</TableCell>
                    <TableCell align="right">{u.profession}</TableCell>
                    <TableCell align="right">{u.consultationFee}</TableCell>
                    <TableCell align="right">{u.salary}</TableCell>
                    <TableCell align="right">{u.room}</TableCell>
                    <TableCell align="right">
                      {u.image ? (
                        <img
                          crossOrigin="anonymous"
                          src={`https://xalilov-project.online/${u.image}`}
                          alt="rasm"
                          style={{ height: "50px" }}
                        />
                      ) : (
                        "No photo"
                      )}
                    </TableCell>
                    <TableCell align="right">
                      {" "}
                      <button
                        className="edit-btn"
                        onClick={() => updateUserData(u.id)}
                      >
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          width="16"
                          height="16"
                          fill="currentColor"
                          className="bi bi-pencil"
                          viewBox="0 0 16 16"
                        >
                          <path d="M12.146.146a.5.5 0 0 1 .708 0l3 3a.5.5 0 0 1 0 .708l-10 10a.5.5 0 0 1-.168.11l-5 2a.5.5 0 0 1-.65-.65l2-5a.5.5 0 0 1 .11-.168l10-10zM11.207 2.5 13.5 4.793 14.793 3.5 12.5 1.207 11.207 2.5zm1.586 3L10.5 3.207 4 9.707V10h.5a.5.5 0 0 1 .5.5v.5h.5a.5.5 0 0 1 .5.5v.5h.293l6.5-6.5zm-9.761 5.175-.106.106-1.528 3.821 3.821-1.528.106-.106A.5.5 0 0 1 5 12.5V12h-.5a.5.5 0 0 1-.5-.5V11h-.5a.5.5 0 0 1-.468-.325z" />
                        </svg>
                      </button>
                    </TableCell>
                    <TableCell align="right">
                      <button
                        className="delete-btn"
                        onClick={() => deleteUser(u.id)}
                        style={{ marginLeft: "20px" }}
                        disabled={u.role == "superadmin" ? true : false}
                      >
                        <svg
                          xmlns="http://www.w3.org/2000/svg"
                          width="16"
                          height="16"
                          fill="currentColor"
                          className="bi bi-trash"
                          viewBox="0 0 16 16"
                        >
                          <path d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z" />
                          <path
                            fillRule="evenodd"
                            d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4 4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"
                          />
                        </svg>
                      </button>
                    </TableCell>
                  </TableRow>
                ))}
              </Table2>

              <div className="pagination">
                <Pagination
                  className="pagination-bar"
                  currentPage={currentPage}
                  totalCount={count}
                  pageSize={allPages}
                  onPageChange={(page) => changeCurrentPage(page)}
                />
              </div>
            </>
          ) : (
            <div className="right-section-details">
              <form onSubmit={onSubmitUserData}>
                <div className="detail">
                  <label>{t("full_name")}</label>
                  <input
                    type={"text"}
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                    required={true}
                  />
                </div>
                <div className="detail">
                  <label>{t("username")}</label>
                  <input
                    type={"text"}
                    value={login}
                    onChange={(e) => setLogin(e.target.value)}
                    required={true}
                  />
                </div>
                <div className="detail">
                  <label>{t("birthday")}</label>
                  <input
                    type={"date"}
                    value={birthday}
                    onChange={(e) => setBirthday(e.target.value)}
                    required={true}
                  />
                </div>
                <div className="detail">
                  <label>{t("degree")}</label>
                  <input
                    type={"text"}
                    value={degree}
                    onChange={(e) => setDegree(e.target.value)}
                    required={true}
                  />
                </div>
                <div className="detail">
                  <label>{t("work_exp")}</label>
                  <input
                    type={"text"}
                    value={workExperience}
                    onChange={(e) => setWorkExperience(e.target.value)}
                    required={true}
                  />
                </div>
                <div className="detail">
                  <label>{t("profession")}</label>
                  <select
                    value={profession}
                    onChange={(e) => setProfession(e.target.value)}
                    required={true}
                  >
                    <option>{t("select_prof")}</option>
                    {category.map((c) => (
                      <option key={c.id} value={c.slug}>
                        {c.category}
                      </option>
                    ))}
                  </select>
                </div>
                <div className="detail">
                  <label>{t("consult_fee")} (so'm)</label>
                  <input
                    type={"number"}
                    value={consultationFee}
                    onChange={(e) => setConsultationFee(e.target.value)}
                    required={true}
                  />
                </div>
                <div className="detail">
                  <label>{t("salary")} (so'm)</label>
                  <input
                    type={"text"}
                    value={salary}
                    onChange={(e) => setSalary(e.target.value)}
                    required={true}
                  />
                </div>
                <div className="detail">
                  <label>{t("room")}</label>
                  <input
                    type={"text"}
                    value={room}
                    onChange={(e) => setRoom(e.target.value)}
                    required={true}
                  />
                </div>
                <div className="detail">
                  <label>{t("password")}</label>
                  <input
                    type={"password"}
                    value={password}
                    onChange={(e) => setPassword(e.target.value)}
                    required={updateUser ? false : true}
                  />
                </div>
                {role !== "superadmin" && (
                  <div className="detail">
                    <label>Role</label>
                    <select
                      value={role}
                      onChange={(e) => setRole(e.target.value)}
                      required={true}
                    >
                      <option>Select Role</option>
                      <option value={"user"}>Doctor</option>
                      <option value={"admin"}>Admin</option>
                      <option value={"cachier"}>Kasser</option>
                    </select>
                  </div>
                )}
                <div className="detail">
                  <label>{t("select_img")}</label>
                  <input
                    type="file"
                    onChange={(e) => setEditImage(e.target.files[0])}
                  />
                </div>
                <div className="update-button">
                  <button>{updateUser ? t("update") : t("create")}</button>
                </div>
              </form>
            </div>
          )}
        </div>
      </div>
    </React.Fragment>
  );
};

export default User;
