import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import InputMask from "react-input-mask";
import Loading from "../../components/Loading/Loading";
import Notification from "../../components/Notification/Notification";
import useRequest from "../../hooks/use-request";
import Table from "../../components/Table/Table";
import { FormControl, FormControlLabel, FormLabel, Radio, RadioGroup } from "@mui/material";
import Pagination from "../../components/Pagination/Pagination";
import "./admin.css";

const Admin = ({ token }) => {
  const { t } = useTranslation();
  const [name, setName] = useState("");
  const [phone, setPhone] = useState("");
  const [type, setType] = useState("");
  const [category, setCategory] = useState([]);
  const [doctors, setDoctors] = useState([]);
  const [doctor, setDoctor] = useState("");
  const [createSore, setCreateSore] = useState(true);
  const [sore, setSore] = useState(null);
  const [reservation, setReservation] = useState([]);
  const [rooms, setRooms] = useState([]);
  const [addRes, setAddRes] = useState(false);
  const [phoneForSearch, setPhoneForSearch] = useState(null);
  const [isCheckDoctor, setIsChectDoctor] = useState(true);
  const [isSelectRoom, setIsSelectRoom] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [count, setCount] = useState(0);
  const [allPages, setAllPages] = useState(0);
  const [roomId, setRoomId] = useState(null);
  const [days, setDays] = useState('');
  const [booking, setBooking] = useState(false);
  const [fullBooking, setFullBooking] = useState(false);
  const [roomReservation, setRoomReservation] = useState([]);
  const [reApply, setReApply] = useState(false);


  const getCategory = useRequest({
    url: "/category",
    method: "get",
    headers: {
      Authorization: `Bearer ${token}`,
    },
    body: {},
    onSuccess: (data) => {
      setCategory(data.data);
    },
  });

  const getUser = useRequest({
    url: `/user/admin?profession=${type}`,
    method: "get",
    headers: {
      Authorization: `Bearer ${token}`,
    },
    body: {},
    onSuccess: (data) => {
      setDoctors(data.data);
    },
  });

  const createSoreRequest = useRequest({
    url: `/sore/doctor/${doctor}`,
    method: "post",
    headers: {
      Authorization: `Bearer ${token}`,
    },
    body: {
      name,
      phone,
      type,
      reApply
    },
    onSuccess: (data) => {
      setSore(data.data);
      setCreateSore(false);
      setReservation(data.data.reservation);
    },
  });

  const createSoreAndToRoomRequest = useRequest({
    url: `/sore/room/${roomId}`,
    method: "post",
    headers: {
      Authorization: `Bearer ${token}`,
    },
    body: {
      name,
      phone,
      days,
      booking,
      fullBooking,
      reApply
    },
    onSuccess: (data) => {
      setSore(data.data.sore);
      setCreateSore(false);
      const reser = [data.data.reservation];
      setRoomReservation(reser);
    },
  });

  const getSoreRequest = useRequest({
    url: `/sore/cachier?phone=${phoneForSearch}`,
    method: "get",
    headers: {
      Authorization: `Bearer ${token}`,
    },
    body: {},
    onSuccess: (data) => {
      setSore(data.data.sore);
      setRoomReservation([]);
      if (data.data.sore.reservation) {
        setReservation(data.data.sore.reservation);
      } else {
        setReservation([]);
      }
    },
  });

  const getRoomsRequest = useRequest({
    url: `/room?isFull=false&limit=4&page=${currentPage}`,
    method: "get",
    headers: {
      Authorization: `Bearer ${token}`,
    },
    body: {},
    onSuccess: (data) => {
      setCount(data.count);
      setAllPages(data.allPages);
      setRooms(data.data);
    },
  });

  const changeCurrentPage = async (page) => {
    setCurrentPage(page);
  };

  useEffect(() => {
    getCategory.doRequest();
    if (type) {
      getUser.doRequest();
    }

    if(currentPage) {
      getRoomsRequest.doRequest();
    }
  }, [type, currentPage]);

  const addReservation = () => {
    setReApply(true)
    setAddRes(true);
    setCreateSore(true);
    setName(sore.name);
    setPhone(sore.phone)
  };

  const onSubmitSoreData = async (e) => {
    e.preventDefault();
    if(isCheckDoctor) {
      await createSoreRequest.doRequest();
    } else {
      await createSoreAndToRoomRequest.doRequest()
    }
  };

  const onSubmitSearch = (e) => {
    e.preventDefault();
    getSoreRequest.doRequest();
  };

  return (
    <React.Fragment>
      {getSoreRequest.loading && <Loading />}
      {getSoreRequest.errors && (
        <Notification
          status={"warning"}
          message={getSoreRequest.errors[0].message}
        />
      )}
      {createSoreRequest.loading && <Loading />}
      {createSoreRequest.errors && (
        <Notification
          status={"error"}
          message={createSoreRequest.errors[0].message}
        />
      )}
      {createSoreAndToRoomRequest.loading && <Loading />}
      {createSoreAndToRoomRequest.errors && (
        <Notification
          status={"error"}
          message={createSoreAndToRoomRequest.errors[0].message}
        />
      )}
      {getRoomsRequest.loading && <Loading />}
      {getRoomsRequest.errors && <Notification
          status={"error"}
          message={getRoomsRequest.errors[0].message}
        />}
      <div className="admin-container">
        <div className="right-section">
          <div className="right-section-header">
            <div
              className={
                !createSore
                  ? "right-section-control"
                  : "right-section-control active"
              }
            >
              <button
                onClick={() => {
                  setCreateSore(true);
                  setReApply(false)
                  setAddRes(false);
                }}
              >
                {addRes ? t("add_application") : t("add_patient")}
              </button>
            </div>
            <div
              className={
                createSore
                  ? "right-section-control"
                  : "right-section-control active"
              }
            >
              <button onClick={() => setCreateSore(false)}>
                {t("patients")}
              </button>
            </div>
          </div>
          {!createSore ? (
            <div className="admin-list">
              <div className="search-section">
                <form onSubmit={onSubmitSearch}>
                  <InputMask
                    mask={"99-999-99-99"}
                    placeholder={"90-999-99-99"}
                    onChange={(e) => setPhoneForSearch(e.target.value)}
                    disabled={false}
                    maskChar="-"
                  />
                  <button>{t("search")}</button>
                </form>
              </div>
              {sore && (
                <div style={{ marginTop: "20px" }}>
                  <Table
                    headers={[
                      t("full_name"),
                      t("phone_number"),
                      "ID",
                      t("re_apply"),
                    ]}
                  >
                    <tr>
                      <td scope="row" data-label={t("full_name")}>
                        {sore.name}
                      </td>
                      <td data-label={t("phone_number")}>{sore.phone}</td>
                      <td data-label="ID">{sore.idNumber}</td>
                      <td data-label={t("re_apply")}>
                        <button
                          style={{
                            padding: "6px 8px",
                            border: "none",
                            outline: "none",
                            color: "#fff",
                            backgroundColor: "rgb(70,84,160)",
                            fontWeight: "500",
                            borderRadius: "4px",
                          }}
                          onClick={addReservation}
                        >
                          {t("add_application")}
                        </button>
                      </td>
                    </tr>
                  </Table>
                  {reservation?.length > 0 && (
                    <>
                      <p
                        style={{
                          margin: "10px",
                          color: "#000",
                          fontSize: "14px",
                        }}
                      >
                        {t("application")}:
                      </p>
                      <Table
                        headers={[
                          t("profession"),
                          t("consult_fee"),
                          t("doctor"),
                          t("room"),
                        ]}
                      >
                        {reservation.map((r) => (
                          <tr key={r.id}>
                            <td scope="row" data-label={t("profession")}>
                              {r.type}
                            </td>
                            <td data-label={t("consult_fee")}>{r.fee}</td>
                            <td data-label={t("doctor")}>{r.doctorName}</td>
                            <td data-label={t("room")}>{r.room}</td>
                          </tr>
                        ))}
                      </Table>
                    </>
                  )}
                  {roomReservation?.length > 0 && <>
                    <>
                      <p
                        style={{
                          margin: "10px",
                          color: "#000",
                          fontSize: "14px",
                        }}
                      >
                        {t("application")}:
                      </p>
                      <Table
                        headers={[
                          t("room"),
                          t("fee"),
                        ]}
                      >
                        {roomReservation.map(r => (
                            <tr key={r.id}>
                              <td scope="row" data-label={t("room")}>
                                {r.roomNumber}
                              </td>
                              <td data-label={t("fee")}>{r.fee}</td>
                            </tr>
                        ))}             
                      </Table>
                    </>
                  </>}
                </div>
              )}
            </div>
          ) : (
            <div className="right-section-details">
              <form onSubmit={onSubmitSoreData}>
                {!addRes && (
                  <>
                    <div className="detail">
                      <label>{t("full_name")}</label>
                      <input
                        type={"text"}
                        value={name}
                        onChange={(e) => setName(e.target.value)}
                        required={true}
                      />
                    </div>
                    <div className="detail">
                      <label>{t("phone_number")}</label>
                      <InputMask
                        mask={"99-999-99-99"}
                        placeholder={"90-999-99-99"}
                        value={phone}
                        onChange={(e) => setPhone(e.target.value)}
                        disabled={false}
                        maskChar="-"
                      />
                    </div>
                  </>
                )}
                <div className="detail">
                <FormControl>
                  <FormLabel id="demo-row-radio-buttons-group-label">{t("select")}</FormLabel>
                  <RadioGroup
                    row
                    aria-labelledby="demo-row-radio-buttons-group-label"
                    name="row-radio-buttons-group"
                  >
                    <FormControlLabel value="doctor" control={<Radio />} label={t("doctor")} checked={isCheckDoctor} onChange={() => setIsChectDoctor(true)} />
                    <FormControlLabel value="room" control={<Radio />} label={t("room")} checked={!isCheckDoctor} onChange={() => {setIsChectDoctor(false); getRoomsRequest.doRequest() }} />
                  </RadioGroup>
                </FormControl>
                </div>
                {isCheckDoctor && 
                     <>
                     <div className="detail">
                       <label>{t("select_prof")}</label>
                       <select
                         value={type}
                         onChange={(e) => setType(e.target.value)}
                         required={true}
                       >
                         <option>{t("select_prof")}</option>
                         {category.map((c) => (
                           <option key={c.id} value={c.slug}>
                             {c.category}
                           </option>
                         ))}
                       </select>
                     </div>
                     {type && (
                       <div className="detail">
                         <label>{t("doctors")}</label>
                         <select
                           value={doctor}
                           onChange={(e) => setDoctor(e.target.value)}
                           required={true}
                         >
                           <option>{t("select_doc")}</option>
                           {doctors.map((d) => (
                             <option key={d.id} value={d.id}>
                               {d.name}
                             </option>
                           ))}
                         </select>
                       </div>
                     )}
                     </>
                }
                {!isCheckDoctor && <>
                  <div className="detail">
                      <Table headers={[t('room_number'), t('room_fee'), t('is_full_room'), t('max_pat_count'),t("room_select")]}>
                      {rooms.map((r) => (
                        <tr key={r.id}>
                          <td scope="row" data-label={t("room_number")}>
                            {r.roomNumber}
                          </td>
                          <td scope="row" data-label={t("room_fee")}>
                            {r.roomFee}
                          </td>
                          <td scope="row" data-label={t("is_full_room")}>
                            {r.patientsCount}
                          </td>
                          <td scope="row" data-label={t("max_pat_count")}>
                            {r.maxPatientsCount}
                          </td>
                          <td scope="row" data-label={t("room_select")}>
                          <FormControl>
                              <RadioGroup
                              aria-labelledby="demo-radio-buttons-group-label"
                              defaultValue="female"
                              name="radio-buttons-group"
                            >
                              <FormControlLabel value="band qilish" control={<Radio />} label={t("booking")} onChange={() => {setIsSelectRoom(true); setRoomId(r.id); setBooking(true); setFullBooking(false)}} />
                              {r.patientsCount == 0 && 
                              <FormControlLabel value="to'liq band qilish" control={<Radio />} label={t("full_booking")} onChange={() => {setIsSelectRoom(true); setRoomId(r.id); setFullBooking(true); setBooking(false);}} />
                              }
                            </RadioGroup>
                          </FormControl>
                          </td>
                        </tr>
                      ))}
                      </Table>
                      <div className="pagination">
                      <Pagination
                        className="pagination-bar"
                        currentPage={currentPage}
                        totalCount={count}
                        pageSize={allPages}
                        onPageChange={(page) => changeCurrentPage(page)}
                />
              </div>
                  </div>
                  {isSelectRoom && <div className="detail">
                    <label>{t("days")}</label>
                      <input
                        type={"number"}
                        value={days}
                        onChange={(e) => setDays(e.target.value)}
                        required={true}
                      />
                    </div>}
                </>}
                <div className="update-button">
                  <button>
                    {addRes ? t("add_application") : t("add_patient")}
                  </button>
                </div>
              </form>
            </div>
          )}
        </div>
      </div>
    </React.Fragment>
  );
};

export default Admin;
