import React, { useContext } from "react";
import i18next from "i18next";
import { NavLink, useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";
import BasicMenu from "../Menu/Menu";
import AuthContext from "../../store/auth-context";

import "./header.css";

const Header = (props) => {
  const authCtx = useContext(AuthContext);
  const navigate = useNavigate();
  const { t } = useTranslation();

  const logutUser = () => {
    authCtx.logout();
    navigate("/");
  };

  return (
    <header className={props.isSidebar ? "header" : "header close-sidebar"}>
      <div className={"left-section"}>
        <button onClick={() => props.setOpenCloseSideBar()}>
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="26"
            height="26"
            fill="currentColor"
            className="bi bi-list"
            viewBox="0 0 16 16"
          >
            <path
              fillRule="evenodd"
              d="M2.5 12a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5zm0-4a.5.5 0 0 1 .5-.5h10a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5z"
            />
          </svg>
        </button>
      </div>
      <div className="right-section">
        <div className="lan">
          <BasicMenu
            header={<p>{t("language")}</p>}
            bottom={[
              <button
                style={{ background: "transparent", border: "none" }}
                onClick={() => i18next.changeLanguage("uz")}
              >
                O'z
              </button>,
              <button
                style={{ background: "transparent", border: "none" }}
                onClick={() => i18next.changeLanguage("ru")}
              >
                Ру
              </button>,
            ]}
          />
        </div>

        <div className="profile">
          <BasicMenu
            header={
              <div className="profile-picture">
                <img
                  crossOrigin="anonymous"
                  src={
                    props.user.image
                      ? `https://xalilov-project.online/${props.user.image}`
                      : "/assets/user.png"
                  }
                  alt="rasm"
                />
              </div>
            }
            bottom={[
              <NavLink
                style={{
                  textDecoration: "none",
                  fontSize: "16px",
                  color: "#111",
                }}
                to={"/profile"}
              >
                {t("profile")}
              </NavLink>,
              <button
                onClick={logutUser}
                style={{
                  background: "transparent",
                  border: "none",
                  fontSize: "16px",
                }}
              >
                {t("logout")}
              </button>,
            ]}
          />
        </div>
      </div>
    </header>
  );
};

export default Header;
