import { Navigate, Route, Routes } from "react-router-dom";
import useRequest from "../../hooks/use-request";
import AuthContext from "../../store/auth-context";
import React, { useContext, useEffect, useState } from "react";
import Layout from "../../components/Layout/Layout";
import Loading from "../../components/Loading/Loading";
import Profile from "../Profile/Profile";
import Category from "../Category/Category";
import User from "../User/User";
import Admin from "../Admin/Admin";
import Cashier from "../Cashier/Cashier";
import Doctor from "../Doctor/Doctor";
import Dashboard from "../Dashboard/Dashboard";
import Patient from "../Patient/Patient";
import History from "../History/History";
import Room from "../Room/Room";
import ControlRoom from "../ControlRoom/ControlRoom";

const Main = ({ token }) => {
  const [user, setUser] = useState(null);
  const authCtx = useContext(AuthContext);

  const getUser = useRequest({
    url: "/auth/user",
    method: "get",
    headers: {
      Authorization: `Bearer ${token}`,
    },
    body: {},
    onSuccess: (data) => {
      setUser(data.data);
    },
  });

  useEffect(() => {
    getUser.doRequest();
  }, []);

  return (
    <React.Fragment>
      {!user ? (
        <Loading />
      ) : (
        <Layout user={user}>
          <Routes>
            <Route
              path="/profile"
              element={
                authCtx.isLoggedIn ? (
                  <Profile
                    user={user}
                    token={authCtx.token}
                    getUser={() => getUser.doRequest()}
                  />
                ) : (
                  <Navigate to={"/"} />
                )
              }
            />
            {user.role === "superadmin" && (
              <Route
                path="/category"
                element={<Category token={authCtx.token} />}
              />
            )}

            {user.role === "superadmin" && (
              <Route path="/users" element={<User token={authCtx.token} />} />
            )}
            {user.role === "admin" && (
              <Route path="/dashboard" element={<Admin token={token} />} />
            )}
            {user.role === "cachier" && (
              <Route path="/dashboard" element={<Cashier token={token} />} />
            )}
            {user.role === "user" && (
              <Route
                path="/dashboard"
                element={
                  <Doctor
                    token={token}
                    user={user}
                    getUser={() => getUser.doRequest()}
                  />
                }
              />
            )}
            {user.role === "superadmin" && (
              <Route path="/dashboard" element={<Dashboard token={token} />} />
            )}
            {user.role === "superadmin" && (
              <Route path="/room" element={<Room token={token} />} />
            )}
            {user.role === "superadmin" && (
              <Route path="/patients" element={<Patient token={token} />} />
            )}
            {user.role === "superadmin" && (
              <Route
                path="/patients/history/:id"
                element={<History token={token} />}
              />
            )}
            {user.role === "admin" && (
              <Route
                path="/admin/rooms"
                element={<ControlRoom token={token} />}
              />
            )}
            <Route path="*" element={<Navigate to="/dashboard" />} />
          </Routes>
        </Layout>
      )}
    </React.Fragment>
  );
};

export default Main;
