import { useState } from "react";
import Sidebar from "../Sidebar/Sidebar";
import "./layout.css";
import Header from "../Header/Header";

const Layout = (props) => {
  const { innerWidth: width } = window;
  const [sidebar, setSidebar] = useState(width > 768 ? true : false);

  const setCloseSidebar = () => {
    setSidebar(false);
  }

  return (
    <div className={"container"}>
      <Sidebar
        isSidebar={sidebar}
        setOpenCloseSideBar={() => setSidebar(!sidebar)}
        setCloseSidebar={setCloseSidebar}
        role={props.user.role}
      />
      <div className={"main"}>
        <Header
          setOpenCloseSideBar={() => setSidebar(!sidebar)}
          isSidebar={sidebar}
          user={props.user}
        />
        <main>
          <div className="main-section">{props.children}</div>
          <div className="buttom">
            <p>
              Produced by
              <a href="https://xolbekxalilov.t.me"> Programmist.uz</a>
            </p>
          </div>
        </main>
      </div>
    </div>
  );
};

export default Layout;
